.date-picker
  width: 170px
  height: 25px
  padding: 0
  border: 0
  line-height: 25px
  padding-left: 10px
  font-size: 12px
  +rl
  cursor: pointer
  color: #303030
  position: relative
  z-index: 2

.date-picker-wrapper
  position: absolute
  z-index: 1
  background-color: #fff
  padding: 5px 12px
  margin: 22px 0 0 52px
  width: 200px
  font-size: 12px
  line-height: 20px
  color: #aaa
  +rl
  box-shadow: 0 0 6px rgba($gray, .15)
  +box
  +r(768)
    margin-left: -15px
  &.inline-wrapper
    position: relative
    box-shadow: none
    display: inline-block
  &.single-date
    width: auto
  &.no-shortcuts
    padding-bottom: 12px
  &.no-topbar
    padding-top: 12px
  .footer
    font-size: 11px
    padding-top: 3px
    display: none
  b
    color: #666
    font-weight: 700
  a
    color: rgb(107, 180, 214)
    text-decoration: underline
  .month-name
    text-transform: capitalize
    color: $black
  .month-wrapper
    border-radius: 3px
    background-color: #fff
    cursor: default
    position: relative
    width: 100% !important
    _overflow: hidden
    table
      width: 100%
      float: left
      &.month2
        width: 190px
        float: left
      th, td
        vertical-align: middle
        text-align: center
        line-height: 14px
        margin: 0px
        padding: 0px
      .day
        padding: 5px 0
        line-height: 1
        font-size: 12px
        margin-bottom: 1px
        color: #ccc
        cursor: default
      div.day
        &.lastMonth, &.nextMonth
          color: #999
          cursor: default
      .day.checked
        background-color: rgb(156, 219, 247)
      .week-name
        height: 20px
        line-height: 20px
        font-weight: 100
        text-transform: uppercase
        display: none
      .day.has-tooltip
        cursor: help !important
  .time label
    white-space: nowrap
  .month-wrapper table .day
    &.toMonth
      &.valid
        color: #333
        cursor: pointer
      &.hovering
        background-color: #cdecfa
    &.nextMonth, &.lastMonth
      display: none
    &.real-today
      background-color: rgb(255, 230, 132)
      &.checked, &.hovering
        background-color: rgb(112, 204, 213)
  table .caption
    height: 40px
    .next, .prev
      padding: 0 5px
      margin: 0
      cursor: pointer
      font-size: 0
      &:hover
        &:before
          transform: translate(0,0)
    .next
      position: relative
      color: $c_blue
      transition: color .2s
      display: inline-block
      &:hover
        &:before
          +arr(7,11,darken($c_blue, 10%),r)
      &:before
        position: absolute
        content: ''
        top: 50%
        left: 0
        margin-top: -6px
        transform: translate3d(0,0,0)
        transition: transform .2s, border-color .2s
        +arr(7,11,$c_blue,r)
    // .next:hover, .prev:hover
      // background-color: #ccc
      // color: white
  .gap
    position: relative
    z-index: 1
    width: 15px
    height: 100%
    background-color: red
    font-size: 0
    line-height: 0
    float: left
    top: -5px
    margin: 0 10px -10px
    visibility: hidden
    height: 0
    .gap-lines
      height: 100%
      overflow: hidden
    .gap-line
      height: 15px
      width: 15px
      position: relative
      .gap-1
        z-index: 1
        height: 0
        border-left: 8px solid white
        border-top: 8px solid #eee
        border-bottom: 8px solid #eee
      .gap-2
        position: absolute
        right: 0
        top: 0px
        z-index: 2
        height: 0
        border-left: 8px solid transparent
        border-top: 8px solid white
      .gap-3
        position: absolute
        right: 0
        top: 8px
        z-index: 2
        height: 0
        border-left: 8px solid transparent
        border-bottom: 8px solid white
    .gap-top-mask
      width: 6px
      height: 1px
      position: absolute
      top: -1px
      left: 1px
      background-color: #eee
      z-index: 3
    .gap-bottom-mask
      width: 6px
      height: 1px
      position: absolute
      bottom: -1px
      left: 7px
      background-color: #eee
      z-index: 3
  .selected-days
    display: none
  .drp_top-bar
    line-height: 1.4
    position: relative
    padding: 10px 40px 10px 0
    .error-top, .normal-top
      display: none
    .default-top
      display: block
    &.error
      .default-top
        display: none
      .error-top
        display: block
        color: red
    &.normal
      .default-top
        display: none
      .normal-top
        display: block
    .apply-btn
      position: absolute
      right: 0px
      top: 6px
      padding: 3px 5px
      margin: 0
      font-size: 12px
      border-radius: 4px
      cursor: pointer
      color: #d9eef7
      border: solid 1px #0076a3
      background: #0095cd
      background: -webkit-gradient(linear, left top, left bottom, from(#00adee), to(#0078a5))
      background: -moz-linear-gradient(top, #00adee, #0078a5)
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00adee', endColorstr='#0078a5')
      color: white
      line-height: initial
      &.disabled
        cursor: pointer
        color: #606060
        border: solid 1px #b7b7b7
        background: #fff
        background: -webkit-gradient(linear, left top, left bottom, from(#fff), to(#ededed))
        background: -moz-linear-gradient(top, #fff, #ededed)
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#ededed')
  .time
    position: relative
  &.single-month .time
    display: none
  .time input[type=range]
    vertical-align: middle
    width: 129px
    padding: 0
    margin: 0
    height: 20px
  .time1
    width: 180px
    padding: 0 5px
    text-align: center

// time styling

.time2
  width: 180px
  padding: 0 5px
  text-align: center

.date-picker-wrapper
  .time1
    float: left
  .time2
    float: right
  .hour
    text-align: right

.minute
  text-align: right

.date-picker-wrapper
  .hide
    display: none
  .first-date-selected, .last-date-selected
    background-color: #49e !important
    color: white !important
  .date-range-length-tip
    position: absolute
    margin-top: -4px
    margin-left: -8px
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3)
    display: none
    background-color: yellow
    padding: 0 6px
    border-radius: 2px
    font-size: 12px
    line-height: 16px
    -webkit-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3))
    -moz-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3))
    -ms-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3))
    -o-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3))
    filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.3))
    &:after
      content: ''
      position: absolute
      border-left: 4px solid transparent
      border-right: 4px solid transparent
      border-top: 4px solid yellow
      left: 50%
      margin-left: -4px
      bottom: -4px
  &.two-months.no-gap
    .month1 .next, .month2 .prev
      display: none
  .week-number
    padding: 5px 0
    line-height: 1
    font-size: 12px
    margin-bottom: 1px
    color: #999
    cursor: pointer
    &.week-number-selected
      color: #4499EE
      font-weight: bold